import { useEffect, useState } from 'react'
import { useParams, useHistory } from 'react-router-dom'

import { useFirebase } from "@meddbriefer/mdb-firebase"
import {
  getCond2DebriefingLog,
  getAnalysisLog,
  getLog,
  getPublishedScenarioObjectByID,
} from "@meddbriefer/scenario-data/index";
import { getScenarioFeedback } from "@meddbriefer/feedback-data";

import { computeDataStructures } from '../dataStructureUtils'
import { DebriefingContext } from '../components/DebriefingContext'

import Cond2Debriefing from '../components/Cond2Debriefing'

// some functions passed down through context which don't make use of state
function computeExpandedSlides(stepPath) {
  const expanded = stepPath
    .split(".")
    .map((item, index, arr) => arr.slice(0, index + 1).join("."));
  return expanded;
}

function stepPathIsExpanded (stepPath, expandedSlides) {
  return expandedSlides.includes(stepPath)
}

export default function DebLog({computeTitle, saveButtonLabel, saveModal}) {
  const {db} = useFirebase()
  const history = useHistory()
  const { debriefingID } = useParams()
  const [debID, setDebID] = useState(null)
  const [ debLog, setDebLog ] = useState(null)
  const [obsLog, setObsLog] = useState(null)
  const [ scenario, setScenario] = useState(null)
  const [slides, setSlides] = useState(null)
  const [slideData, setSlideData] = useState(null)
  const [pathData, setPathData] = useState(null)
  const [idData, setIdData] = useState(null)
  const [userRatings, setUserRatings] = useState({})
  const [ieSummary, setIESummary] = useState(null)
  const [bOutline, setBOutline] = useState(null)
  const [ieOutline, setIEOutline] = useState(null)
  const [loaded, setLoaded] = useState(false)
  const [allSlidesCompleted, setAllSlidesCompleted] = useState(false)
  const [done, setDone] = useState(false)

  const getIdData = (id) => idData[id]

  const updateIdData = (id, newValue) => {
    const newData = {...idData[id], newValue}
    setIdData(prevState => ({
      ...prevState,
      [id]: newData
    }))
  }

  // some useful accessor methods
  const getSlideFromStepStepPath = (step, stepPath) => {
    return slides.find(e => e.step === step && e.stepPath === stepPath)
  }
  const getSlideIndexForStepStepPath = (step, stepPath) => {
    return slides.findIndex((e) => e.step === step && e.stepPath === stepPath);
  }

  const getSlideData = (step, stepPath) => {
    const slide = getSlideFromStepStepPath(step, stepPath)
    return slideData[slide.id]
  }

  const getUserRating = (id) => userRatings[id]
  
  const setUserRating = (id, value) => {
    setUserRatings(prevValue => ({
      ...prevValue,
      [id]: value
    }))
  }

  const stepStepPathToPath = (step, stepPath) => `${step}/${stepPath}`

  const getPathData = (step, stepPath) => {
    const path = stepStepPathToPath(step, stepPath)
    return {...pathData[path]}
  }

  // const updatePathState = (path, newPathData) => {
  //   setPathData(prevState => ({
  //     ...prevState,
  //     [path]: newPathData
  //   }))
  // }

  const updatePathData = (step, stepPath, newData) => {
    const path = stepStepPathToPath(step, stepPath)
    const updatedPathData = {...getPathData(step, stepPath), ...newData}
    setPathData(prevState => ({
      ...prevState,
      [path]: updatedPathData
    }))
  }

  // const enableStepPath = (step, stepPath) => {
  //   const path = stepStepPathToPath(step, stepPath)
  //   const currPathData = getPathData(step, stepPath)
  //   if (!currPathData.enabled) {
  //     console.log(`enabling path ${path}`)
  //     currPathData.enabled = true
  //     updatePathState(path, currPathData)
  //   } else {
  //     console.log(`path ${path} already enabled`)
  //   }
  // }

  const enableStepPath = (step, stepPath) => updatePathData(step, stepPath, { enabled: true })
    
  const enableNextSlide = (currStep, currStepPath) => {
    console.log(`enableNextSlide${currStep}, ${currStepPath}`)
    const currSlideIdx = getSlideIndexForStepStepPath(currStep, currStepPath)
    if (currSlideIdx+1 === slides.length) {
      console.log("no next slide to enable. skipping enableNextSlide()")
    } else {
      const nextSlide = slides[currSlideIdx+1]
      if (!stepStepPathIsEnabled(nextSlide.step, nextSlide.stepPath)) {
          enableStepPath(nextSlide.step, nextSlide.stepPath);
      }
    }
  }
  
  const havePrevSlide = (currStep, currStepPath) => {
    return getSlideIndexForStepStepPath(currStep, currStepPath) !== 0
  }

  const prevSlide = (currStep, currStepPath) => {
    const idx = getSlideIndexForStepStepPath(currStep, currStepPath);
    const prev = slides[idx -1];
    goStepStepPath(prev.step, prev.stepPath);
  }
  
  const haveNextSlide = (currStep, currStepPath) => {
    let haveNext
    const currSlideIdx = getSlideIndexForStepStepPath(currStep, currStepPath);
    
    if (currSlideIdx + 1 === slides.length) {
      haveNext = false;
    } else {
      const nextSlide = slides[currSlideIdx+1]
      haveNext = stepStepPathIsEnabled(nextSlide.step, nextSlide.stepPath)
    }
    // console.log(`haveNextSlide(${currStep}, ${currStepPath}) =`, haveNext)
    return haveNext
  };

  const nextSlide = (currStep, currStepPath) => {
    const idx = getSlideIndexForStepStepPath(currStep, currStepPath)
    const next = slides[idx+1]
    goStepStepPath(next.step, next.stepPath)
  }
  const goStepStepPath = (step, stepPath) => { 
    const newURL = `/debriefing/${debriefingID}/${step}/${stepPath}`
    console.log("redirecting to", newURL)
    history.push(newURL)
  }

  const stepIsEnabled = (step) => {
    const enabledStepPaths = Object.values(pathData).filter(e => e.step === step && e.enabled)
    const someEnabled = enabledStepPaths.length > 0
    // console.log("stepIsEnabled", step, enabledStepPaths, someEnabled)
    return someEnabled
  }

  const stepStepPathIsEnabled = (step, stepPath) => {
    // const slide = getSlideFromStepStepPath(step, stepPath)
    // const path = `${slide.step}/${slide.stepPath}`
    const path = stepStepPathToPath(step, stepPath)
    return pathData[path].enabled
  };


  const fetchData = async () => {
    const debLogData = await getCond2DebriefingLog(db, debriefingID);
    const analysisData = await getAnalysisLog(
      db,
      debLogData.analysisLogID
    );
    const obsLogData = await getLog(db, analysisData.observerLogID);
    const scenData = await getPublishedScenarioObjectByID(
      db,
      obsLogData.scenarioID
    );
    const scenFeedBack = getScenarioFeedback(scenData.name);
    const { stepSlides, sData, pthData, idDat, summaryObj, bOL, ieOL } =
      computeDataStructures(
        scenData,
        obsLogData,
        analysisData,
        debLogData,
        scenFeedBack
      );
    setScenario(scenData);
    setDebLog(debLogData);
    setObsLog(obsLogData)
    setSlides(stepSlides);
    setSlideData(sData);
    setPathData(pthData);
    setIdData(idDat);
    setIESummary(summaryObj);
    setBOutline(bOL);
    setIEOutline(ieOL);
    setLoaded(true);
    setAllSlidesCompleted(idDat.formalize.response !== "")
    setDone(false)
  };

  useEffect(
    () => {

      if (!debID || debID !== debriefingID) {
        setDebID(debriefingID)
        if (!loaded) {
        fetchData()
          .then(() => console.log("data fetched"))
          .catch(error => console.error(error))
        }
      }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []
  )
  
  if (!loaded) {
    return (<h3>Loading</h3>)
  }

  return (
    <DebriefingContext.Provider
      value={{
        debriefingID,
        debLog,
        obsLog,
        scenario,
        getSlideData,
        getPathData,
        updatePathData,
        getIdData,
        updateIdData,
        stepStepPathToPath,
        havePrevSlide,
        haveNextSlide,
        prevSlide,
        nextSlide,
        enableNextSlide,
        enableStepPath,
        stepIsEnabled,
        stepStepPathIsEnabled,
        goStepStepPath,
        getUserRating,
        setUserRating,
        ieSummary,
        bOutline,
        ieOutline,
        computeExpandedSlides,
        stepPathIsExpanded,
        allSlidesCompleted,
        setAllSlidesCompleted,
        done,
        setDone
      }}
    >
      <Cond2Debriefing 
        debriefingID={debriefingID}
        computeTitle={computeTitle}
        saveButtonLabel={saveButtonLabel} 
        saveModal={saveModal} 
      />
    </DebriefingContext.Provider>
  );

}
