function buildBOutline(feedback, slides, slideData) {
  const outline = {
    heading: "Expected completed actions:",
    children: [],
  };
  feedback.B.expectations.forEach((node, idx) => {
    const item = {
      ...slides.find(
        (rec) => rec.step === "B" && rec.stepPath === `${idx + 1}`
      ),
    };
    delete item.enabled;
    delete item.childIDs;
    item.children = [];
    item.label = node.expectationText;
    // console.log(item)
    outline.children.push(item);
  });
  return outline;
}

function buildIEOutline(node, slides, slideData) {
  const { id, step, stepPath, label, childIDs } = node;
  const nodeData = { id, step, stepPath, label };
  nodeData.children = childIDs
    .filter((childID) => slides.find((rec) => rec.id === childID))
    .map((childID) =>
      buildIEOutline(
        slides.find((rec) => rec.id === childID),
        slides,
        slideData
      )
    );
  return nodeData;
}

function computeSlides(feedback, summaryObj) {
  const obj = {
    D: [{ id: "define", step: "D", stepPath: "1", childIDs: [] }],
    E: [{ id: "explain", step: "E", stepPath: "1", childIDs: [] }],
    B: feedback.B.expectations.map((e, idx) => ({
      id: `B${idx + 1}`,
      step: "B",
      stepPath: `${idx + 1}`,
      childIDs: [],
    })),
    IE: createIESlides(feedback, summaryObj),
    R: feedback.R.slides.map((slide, idx) => ({
      id: `R${idx + 1}`,
      step: "R",
      stepPath: `${idx + 1}`,
      childIDs: [],
    })),
    F: [{ id: "formalize", step: "F", stepPath: "1", childIDs: [] }],
  };
  return [...obj.D, ...obj.E, ...obj.B, ...obj.R, ...obj.IE, ...obj.F];
}

function computeSlideData(feedback, summaryObj) {
  const obj = {
    //D
    define: { ...feedback.D },
    // E
    explain: { ...feedback.E },
    //F
    formalize: { ...feedback.F },
  };
  let rSlidesData = {};
  feedback.R.slides.forEach((slide, idx) => {
    rSlidesData[`R${idx + 1}`] = {
      ...slide,
      of: [idx + 1, feedback.R.slides.length],
    };
  });
  let bSlidesData = {};
  feedback.B.expectations.forEach((exp, idx) => {
    const id = `B${idx + 1}`;
    bSlidesData[id] = { ...exp };
  });
  const ieSlideData = createIESlidesData(feedback, summaryObj);
  return Object.assign({}, obj, rSlidesData, bSlidesData, ieSlideData);
}

