import React from "react";
import { Table, Button } from "reactstrap";

import { useDebriefing } from "../DebriefingContext";

import AnalysisMessages from "./AnalysisMessages";

import styles from "../Cond2Debriefing.module.css";

export default function Analysis({ slide, stepPath, getRequiredSubActions, analyzedHandler }) {
  const { getPathData, ieSummary } = useDebriefing();
  // console.log("Analysis", slide)

  const getStatus = (id) => {
    const sts = ieSummary[id].status;
    // console.log(`status for ${id}: ${sts}`)
    return sts;
  };
  const isGood = (id) => "good" === getStatus(id);
  const isAbsent = (id) => "absent" === getStatus(id);
  const hasError = (id) => ["error", "errors", "misorderedError"].includes(getStatus(id))
  const isOutOfOrder = (id) => ["misordered", "misorderedError"].includes(getStatus(id))

  return (
    <>
      <h4>Analysis based off of what the observer recorded you doing</h4>
      <div
        className={styles["feedback-list"]}
        style={{
          maxHeight: "500px",
          overflowY: "auto",
        }}
      >
        <Table size="sm" striped={true} bordered={true}>
          <thead style={{ height: "28px", backgroundColor: "white", position: "sticky", top: 0 }}>
            <tr>
              <th>Action</th>
              <th>Correct</th>
              <th>Feedback</th>
            </tr>
          </thead>
          <tbody>
            {getRequiredSubActions(slide).map((sa, idx) => (
              <tr key={idx}>
                <td>{sa.label}</td>
                <td>
                  {isGood(sa.id) && (
                    <i
                      className="bi-check-lg"
                      style={{
                        color: "green",
                      }}
                    ></i>
                  )}
                </td>
                <td>
                  <AnalysisMessages
                    errors={hasError(sa.id) ? sa.feedbackErrors : []}
                    absent={isAbsent(sa.id) ? sa.feedbackAbsent : []}
                  />
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      </div>
      {isOutOfOrder(slide.id) && (
        <div>
          <h5>The following steps were performed out of order:</h5>
          {slide.feedbackOutOfOrder.map((para, idx) => (
            <p key={idx}>{para}</p>
          ))}
        </div>
      )}
      <Button 
        size="sm" 
        color="primary" 
        onClick={analyzedHandler}
        disabled={getPathData("IE", stepPath).analyzed}
      >
        Ok
      </Button>
    </>
  );
}
