import {
    Switch,
    useHistory
} from "react-router-dom";

import {useFirebaseProject, FirebaseContext} from "@meddbriefer/mdb-firebase"
import { AuthProvider, PrivateRoute } from "@meddbriefer/mdb-auth";

import { DebLog, UnauthedLayout } from "@meddbriefer/cond2-debriefing-ui"
import { LogChooser } from "@meddbriefer/log-chooser"

import SaveModal from "./SaveModal";

import "./styles.css";

import { environment } from "./environments/environment";


const computeTitle = (obsLog, scenario) => {
  return `${scenario.name} / ${obsLog.userName} / ${obsLog.label}`
}

export default function App() {
    const {auth, db} = useFirebaseProject(environment.firebaseProject)
    let history = useHistory()
    window.db = db;
    
    function redirectToCond2Debriefing(debriefID) {
      const newURL = `/debriefing/${debriefID}`
      history.push(newURL)
    }

    return (
      <FirebaseContext.Provider
        value={{
          auth,
          db,
        }}
      >
        <AuthProvider>
          <Switch>
            <PrivateRoute
              path="/debriefing/:debriefingID"
              authFlowLayout={UnauthedLayout}
            >
              <DebLog
                computeTitle={computeTitle}
                saveButtonLabel="Save"
                saveModal={SaveModal}
              />
            </PrivateRoute>
            {/* catchall route - no path needed?*/}
            <PrivateRoute authFlowLayout={UnauthedLayout}>
              <LogChooser
                redirectToCond2Debriefing={redirectToCond2Debriefing}
              />
            </PrivateRoute>
          </Switch>
        </AuthProvider>
      </FirebaseContext.Provider>
    );
}