import React, {useEffect, useState} from "react"

import { Button } from "reactstrap"

export default function AnalysisMessages({errors, absent}) {
    const [msgs, setMsgs] = useState(null)
    const [showLess, setShowLess] = useState(false)

    useEffect(
        () => {
            const tmp = [errors, absent].flat()
            const tmp2 = tmp.filter(msg => msg !== "")
            // console.log("errors", errors, "absent", absent, "flattened", tmp, "filtered", tmp2, "length > 1", tmp2.length > 1);
            setMsgs(tmp2)
            setShowLess(tmp2.length > 1)
        },
        [errors, absent]
    )

    if (!msgs) {
        return <div>calculating</div>
    }
    if (msgs.length === 0) {
        return <span></span>
    }
    return (
        <div>
        {showLess
          ? (
              <>   
              <p>
                  {msgs[0]}
              </p>
              <Button 
                  color="link" 
                  size="sm" 
                  onClick={() => setShowLess(false)}
              >
                  show more
              </Button>
              </>
          )
          : (
              <>
              {msgs.map((msg, i) =>
                  <p key={i}>
                  {msg}
                  </p>
              )}
              {msgs.length > 1 &&
              <Button
                color="link"
                size="sm"
                onClick={() => setShowLess(true)}
              >
                show less
              </Button>
              }
              </>
          )
        }
        </div>
        
    )
}