import React  from "react";

import { useDebriefing } from "../DebriefingContext";

import OutlineButton from "./OutlineButton";
import OutlineLabel from "./OutlineLabel";

export default function OutlineChildren({ currStep, currStepPath, node, expandedSlides, depth=0 }) {
  const { stepPathIsExpanded} = useDebriefing();

  const expandChildren = (node) => {
    const shouldExpand =  node.children.length > 0 && stepPathIsExpanded(node.stepPath, expandedSlides)
    // if () {
    //   shouldExpand = false
    // } else {
    //   shouldExpand = 
    // }
    // console.log(`expandChildren() stepPath: ${node.stepPath}, #children ${node.children.length} , expandedSlides: ${JSON.stringify(expandedSlides, null, 0)}, shouldExpand: ${expand}`)
    return shouldExpand
  } 


  return (
    <ul
      style={{
        paddingLeft: "10px",
        listStyle: "none",
      }}
    >
      {node.children.map((child) => (
        <li
          key={child.id}
          style={{
              marginBottom: "2px",
          }}
        >
          {currStepPath === child.stepPath 
          ? <OutlineLabel node={child} depth={depth} />
          : <OutlineButton node={child} currStep={currStep} currStepPath={currStepPath} depth={depth} />
          }

          {expandChildren(child) && 
            <OutlineChildren
              currStep={currStep} 
              currStepPath={currStepPath}
              node={child}
              expandedSlides={expandedSlides}
              depth={depth+1}
            />
          }
        </li>
      ))}
    </ul>
  );
}
