import React from "react";
import classNames from "classnames";

import { useDebriefing } from "../DebriefingContext";

import { capitalizeFirstLetter, ellipsis } from "../../utils";

import styles from "../Cond2Debriefing.module.css"

const MAX_LENGTH = 40

export default function OutlineButton({ node, currStep, currStepPath, depth=0 }) {
  const { goStepStepPath, stepStepPathIsEnabled } = useDebriefing();

  return (
    <button
      type="button"
      className={classNames("btn", "btn-link", styles["btn-link"])}
      disabled={!stepStepPathIsEnabled(node.step, node.stepPath)}
      style={{
        textAlign: "left",
        paddingLeft: 0,
        paddingRight: 0,
        paddingTop: "2px",
        paddingBottom: "2px",
        border: 0,
        textOverflow: "ellipsis",
      }}
      onClick={() => goStepStepPath(node.step, node.stepPath)}
    >
      {ellipsis(capitalizeFirstLetter(node.label), (MAX_LENGTH - (depth * 3)))}
    </button>
  );
}
