import React, { useCallback, useEffect, useState } from "react";
 
import { useParams } from "react-router-dom";

import { useDebriefing } from "../DebriefingContext";

import TwoDivLayout from "../layouts/TwoDivLayout";
import HierarchicalLeftNav from "../outline/HierarchicalLeftNav";
import UserRating from "./UserRating";
import Analysis from "./Analysis";
import ExamineForm from "./ExamineForm";

import styles from "../Cond2Debriefing.module.css";

export default function InvestigateExamine() {
  const { stepPath } = useParams();
  const { 
    enableNextSlide, 
    getPathData,
    updateIdData,
    updatePathData,
    getSlideData, 
    ieOutline, 
    computeExpandedSlides
  } = useDebriefing();

  const [sp, setSP] = useState(null)
  const [expandedSlides, setExpandedSlides] = useState(null)
  const [needToExamine, setNeedToExamine] = useState(null)
  const [rated, setRated] = useState(null)
  const [analyzed, setAnalyzed] = useState(null)
  const [examined, setExamined] = useState(null)
  const [myData, setMyData] = useState(null)
  const [mode, setMode] = useState(null)
  const [computeMode, setComputeMode] = useState(false)

  const getMode = () => mode

  const getRequiredSubActions = (slide) =>
    slide.subActions.filter((a) =>
      ["header", "required"].includes(a.type.toLowerCase())
    );

  const onRatingComplete = () => {
    setRated(true)
    updatePathData("IE", stepPath, { rated: true });
    setComputeMode(true)
  }

  const onAnalysisComplete = (event) => {
    event.preventDefault()
    setAnalyzed(true)
    updatePathData("IE", stepPath, { analyzed: true });
    setComputeMode(true)
  }

  const onExamineComplete = (examineResponse) => {
    setExamined(true)
    updateIdData(myData.id, {examine: examineResponse})
    updatePathData("IE", stepPath, {examined: true})
    setComputeMode(true)
  }



  useEffect(
    () => { 
      if(!sp || sp !== stepPath) {
        setSP(stepPath)
        console.log("stepPath set")
        // setPath(stepStepPathToPath("IE", stepPath));
        setExpandedSlides(computeExpandedSlides(stepPath));
        const slide = getSlideData("IE", stepPath);
        // console.log(slide)
        setMyData(slide);
        setNeedToExamine(slide.examine);
        const pd = getPathData("IE", stepPath)
        console.log(pd)
        const {rated: _rated, analyzed: _analyzed, examined: _examined} = pd
        setExamined(_examined);
        setAnalyzed(_analyzed);
        setRated(_rated)
        setMode(null)
        setComputeMode(true)
      }
    }, 
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [stepPath] //stepPath, sp, getSlideData, computeExpandedSlides, stepStepPathToPath]
  )

  // useEffect(
  //   () => {
  //     if (rated !== null && analyzed !== null && examined !== null) {
  //       console.log("analyzed", analyzed, "examined", examined, "rated", rated);
  //       computeMode()
  //     }
  //   }, 
  //   [analyzed, examined, rated, computeMode]
  // );

  useEffect(
    () => {
      if (!computeMode || rated === null || analyzed === null || examined === null) {
        console.log(`skipping computation of mode: computeMode: ${computeMode} rated: ${rated} analyzed: ${analyzed} examined: ${examined}`)
      } else {
        console.log(
          `compututing mode: rated: ${rated} analyzed: ${analyzed} examined: ${examined}`
        );
        let newMode;
        if (examined) {
          newMode = "examine";
          console.log("examined=true calling enableNextSlide()");
          enableNextSlide("IE", stepPath);
        } else if (analyzed) {
          if (needToExamine) {
            newMode = "examine";
          } else {
            newMode = "analysis";
            console.log(
              "analyed=true && !needToExamine calling enableNextSlide()"
            );
            enableNextSlide("IE", stepPath);
          }
        } else if (rated) {
          console.log("rated=true");
          newMode = "analysis";
        } else {
          console.log("rated=false");
          newMode = "rate";
        }

        if (newMode !== getMode()) {
          console.log("changing mode to", newMode);
          setMode(newMode);
        } else {
          console.log("keeping mode", getMode());
        }
        setComputeMode(false)
      }
    }, [computeMode, stepPath, rated, analyzed, examined, needToExamine, getMode, enableNextSlide]
  )



  if (!myData || !mode) {
    return <div>loading...</div>
  }


  return (
    <TwoDivLayout
      currStep="IE"
      currStepPath={stepPath}
      outline={
        <HierarchicalLeftNav
          currStep="IE"
          currStepPath={stepPath}
          outline={ieOutline}
          expandedSlides={expandedSlides}
        />
      }
    >
      <div className={styles['feedback-block']}>
        {myData.subActions.length > 0 && (
          <>
            {"rate" === mode && (
              <UserRating
                slide={myData} 
                getRequiredSubActions={getRequiredSubActions}
                ratedHandler={onRatingComplete}
              />
            )}
            {"analysis" === mode && (
              <Analysis
                slide={myData}
                stepPath={stepPath}
                getRequiredSubActions={getRequiredSubActions}
                analyzedHandler={onAnalysisComplete} 
              />
            )}
            {"examine" === mode &&
              <ExamineForm
                slide={myData}
                stepPath={stepPath}
                submitHandler={onExamineComplete}
              />
            }
          </>
        )}
      </div>
    </TwoDivLayout>
  );
}
